.about-outer-container {
  margin: 80px 0 0 0;
}

/*ABOUT HERO*/
.about-hero {
  min-height: 400px;
  align-content: center;
  margin-bottom: 50px;
  position: relative;
  width: 100%;
  align-items: center;
  color: white;
  overflow: hidden;
  /* background-color: black; */
  z-index: -2;
  background-image: url(../images/coding-video-poster.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.hero-video {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.02);
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: 300px;
  z-index: -1;
  filter: blur(10px) brightness(0.5);
  object-fit: cover;
}


.about-hero-all-text {
  padding: 5px 20px;
  margin: 0 auto;
  max-width: 1000px;
}

.about-hero .about-name {
  font-size: 2.5em;
  font-weight: 400;
  padding: 0;
}

.about-hero h3{
  min-height: 80.4px;

}

.about-hero .role {
  letter-spacing: 2px;
  color: var(--primary-color);
}

/*STANDARDS*/
.about-container {
  padding: 0 20px;
  margin: auto;
  max-width: 600px;
  max-width: 1000px;
}

.about-container h2 {
  font-weight: 500;
  margin-bottom: 0.5em;
  text-transform: lowercase;
}

.new-container {
  margin-top: 50px;
}

/*SECTION ABOUT INTRO*/
.about-container-intro {
  display: flex;
  flex-direction: column;
  /*flex-wrap: wrap;*/
  gap: 20px;
  width: 100%;
}

.about-container-text {
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.externallink-icon {
  margin-right: 0.5em;
}

.about-container-btn {
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
}

.about-btn {
  font-size: 0.9em;
  text-decoration: none;
  text-align: center;
  padding: 10px;
  display: flex;
  justify-content: center;
}

.about-btn>span {
  display: flex;
  align-items: center;
}

.about-image-container {
  /*margin-top: 1em;*/
  margin: auto;
}

.about-container .about-image {
  width: 100%;
  max-width: 420px;
}

/*SECTION FUN FACTS*/
.funfacts-inner-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  gap: 20px;
}

.funfacts-inner-container .funfact-item {
  background-color: #f1f1f1;
  padding: 20px;
  width: calc(50% - 10px);
  width: 100%;
  min-height: 121px;
  text-align: center;
  align-content: center;
}

.funfacts-inner-container .funfact-icon {
  width: 30px;
  height: auto;
  margin-bottom: 5px;
}

/*SECTION CURRENT DOING*/
.current-container {
  min-height: 200px;
  align-content: center;
}

.current-container>div {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  justify-content: space-between;
  gap: 20px;
}

.color-line{
  width: 50px;
  background-color: var(--primary-color);
  height: 1.5px;
  border: none;
  border-radius: 5px;
  margin: auto;
}

.current-container .current-text {
  max-width: 400px;
  display: flex;
  flex-direction: column;
  gap: 1em;
}

