/*SECTION EXPERIENCES*/
.language-container>p {
  margin-bottom: 1em;
}

.language-inner-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.about-language-item {
  background-color: #f1f1f1;
  padding: 20px;
  width: calc(50% - 10px);
  width: 100%;
  min-height: 110px;
  text-align: center;
  align-content: center;
}

.language-container h4 {
  margin-bottom: 0.5em;
}

.language-divider {
  color: var(--primary-color);
}

.show-all-btn {
  font-size: 0.9em;
}

/*SMALL SCREEN*/
@media screen and (min-width: 540px) {
  .about-hero .about-name {
    font-size: 3em;
  }

  .about-container-btn {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  .about-btn {
    width: calc(33% - 6px);
  }

  .funfacts-inner-container .funfact-item {
    width: calc(50% - 10px);
    min-height: 180px;
    align-content: start;
    padding-top: 40px;
  }

}

/*TABLET*/
@media screen and (min-width: 768px) {
  .new-container {
    margin-top: 100px;
  }

  .about-hero-all-text {
    padding: 5px 50px;
  }

  .about-container {
    padding: 0 50px;
  }

  .about-container-intro {
    flex-direction: row;
  }

  .about-container-text {
    flex: 1;
  }

  .about-image-container {
    display: flex;
    flex: 1;
    max-width: 270px;
    max-width: 286px;
    margin: 0;
    margin-top: auto;
  }

  .about-container .about-image {
    width: 100%;
    height: auto;
  }

  .connect-text {
    margin-top: auto;
  }

  .current-container>div {
    flex-direction: row;
    text-align: start;
    background: linear-gradient(to right, rgb(241, 241, 241), rgb(255, 255, 255));
  }

  .color-line{
    margin: 0;
  }

  .current-container .current-text {
    max-width: 50%;
    max-width: none;
    flex: 1;
  }


}

/*DESKTOP*/
@media screen and (min-width: 992px) {
  .about-container h1 {
    text-align: center;
  }

  .funfacts-inner-container .funfact-item {
    width: calc(33% - 11px);
  }

  .about-language-item {
    width: calc(50% - 10px);
  }

}